:root {
  --background-color: var(--background-color);
  --text-color: white;
}

html,
body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;
  background-color: var(--background-color);
  color: var(--text-color);
}

::selection {
  background-color: #357cf2;
  color: white;
}

ul {
  list-style-type: none;
  padding: 0;
}

a {
  text-decoration: none;
  color: var(--text-color);
  font-size: 0.875rem;
  font-weight: 300;

  @media screen and (max-width: 700px) {
    font-size: 0.7rem;
  }
}

img {
  width: 100%;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
}

h1 {
  font-size: 3.5rem;
  font-weight: 700;

  @media screen and (max-width: 700px) {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.2rem;
  font-weight: 500;
}

h3 {
  font-size: 1rem;
  font-weight: 500;
}

h4 {
  font-size: 0.875rem;
  font-weight: 700;
}

h5 {
  font-size: 0.8rem;
  font-weight: 500;
}

p {
  font-size: 0.7rem;
  font-weight: 300;
  opacity: 0.8;
  line-height: 180%;
}

.Container {
  max-width: 1300px;
  padding: 0 1.5em 0;
  margin: auto;
  position: relative;

  @media screen and (min-width: 1050px) {
    padding: 0 8em;
  }
}
